@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);

* {
  margin: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}
button {
  font-family: 'Montserrat', sans-serif;
}
body {
  font-family: 'Montserrat', sans-serif;
  background-color: #f8f8f8;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  width: 95%;
}
.App {
  text-align: center;
  display: flex;
  margin-top: 100px;
}
footer {
    border-top: 1px solid #000;
    margin-top: 3rem;
    padding: 2rem 0 2rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  footer p {
    font-weight: 500;
    max-width: 600px;
    margin-right: 0.5rem;
  }
  
  @media (max-width: 576px) {
    footer p {
      font-size: 0.8rem;
    }
  }
.header-content_logo {
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
  }
  .header-content_logo img {
    width: 1.5rem;
    margin-right: 0.4rem;
  }
  .header-content_logo > div {
    display: flex;
    align-items: center;
  }
  .header-content_logo > p {
    font-size: 0.5rem;
  }
  .content-logo img {
    width: 1.5rem;
  }
 .logo:hover{
    cursor: pointer;
    box-shadow: 0px 0px 10px grey;
  }
.btnAddRemove {
    display: flex;
    align-items: center;
    width: 50px;
    justify-content: center;
    border-radius: 100px;
    padding: 0.3rem;
    cursor: pointer;
    font-size: 0.6rem;
    background: #000;
    color: #fff;
  }
  .btnAddRemove-positive,
  .btnAddRemove-negative {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
  }
.item {
    width: 160px;
    padding: 0 0.8rem 0.8rem 0.8rem;
    background-color: #fff;
    border-radius: 30px;
    text-align: center;
    flex-direction: column;
    display: flex;
  }
  
  .item img {
    margin-top: -3.5rem;
    height: 150px;
    object-fit: contain;
  border-radius: 0px 10px;
  }
  .item-foot_desc {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .item-head_desc p {
    margin: 0;
    margin-bottom: 0.5rem;
  }
  
  .item-foot_desc button {
    background: #000;
    color: #fff;
    border-radius: 50%;
  }
  .item-head_desc {
    text-align: left;
    flex: 1 1;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  
  .head_desc-name {
    font-weight: 600;
    font-size: 0.9rem;
    text-transform: capitalize;
    flex: 1 1;
  }
  .head_desc-info {
    font-size: 0.8rem;
    color: #a0a0a0;
  }
  .foot_desc-price {
    font-weight: 700;
    ;
  }
  .item img:hover{
    
    -webkit-animation:slideOutDown;
    
            animation:slideOutDown;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    cursor: pointer;
    box-shadow: 0px 0px 10px brown;

  }
main {
    padding-top: 3.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    justify-items: center;
    grid-row-gap: 5rem;
  }
  
  @media (max-width: 935px) {
    main {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
  
  @media (max-width: 735px) {
    main {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  
  @media (max-width: 535px) {
    main {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  @media (max-width: 335px) {
    main {
      grid-template-columns: 1fr;
    }
  }
.btnCartCount {
    width: 50px;
    height: 50px;
    background: #000;
    color: #fff;
    border-radius: 15px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 1.5rem;
    cursor: pointer;
    right: 1rem;
  }
  
  .btnCartCount .count {
    position: absolute;
    background-color: red;
    top: -8px;
    left: -9px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    font-size: 0.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
  }
  .btnCartCount  i{
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0.5rem 0 0 0;
    height: 100vh;
  }
  .header-img {
    width: 100%;
    align-self: center;
  }
  .header-content {
    display: flex;
    flex-direction: column;
  }
  
  .content-main {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    justify-content: center;
  }
  .content-main h1 {
    font-size: 3rem;
    margin-bottom: 1.5rem;
  }
  .content-main button {
    background: #000;
    color: #fff;
    width: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.5rem;
    outline: none;
    border: none;
    font-size: 1rem;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    cursor: pointer;
    margin-top: 2.5rem;
  }
  
  .content-main p {
    font-weight: 500;
  }
  
  @media (max-width: 768px) {
    .content-main h1 {
      font-size: 1.2rem;
      margin-bottom: 1rem;
    }
    .content-main p {
      font-size: 0.8rem;
    }
    .content-main button {
      font-size: 0.8rem;
      width: 150px;
      padding: 0.5rem 1rem;
      margin-top: 1.5rem;
    }
  }
